#footer {
    width: 100vw;
    font-size: 35px;
    justify-content: center;
    margin: 0px 0px 10px;
    display: flex;
    flex-direction: column;

    a {
        color: white;
        margin: 0 15px;
    }

    p {
        color: white;
        font-size: 20px;
        top: 10px;
    }

    a:hover {
        color: rgb(255, 226, 243);
    }
}

@media screen and (max-width: 440px) {
    .footer {
        padding-bottom: 30px;
        margin-bottom: 0;
    }
}