.navbar {
    width: 100vw;
    height: 100px;
    display: flex;
    justify-content: space-between;
    position: fixed;
    z-index: 100000;
    background-image: linear-gradient(rgb(213, 157, 190), rgba(255, 162, 176, 0));
    top: 0;
    transition: all .2s ease;

    .menu-button {
        display: none;
        left: 30px;
        top: 30px;
        position: absolute;
        background-color: #fff;
        color: rgb(255, 188, 249);
        border-color: #fff;
        border-radius: 15px;
        font-size: 1em;
        border-style: solid;
    }
    
    a {
        text-decoration: none;
    }
    
    p {
        color: white;
        font-size: 20px;
        font-weight: bold;
        margin-left: 30px;
        margin-bottom: 0px;
        line-height: 38px;
        width: 100%;
    }

    p:hover {
        cursor: pointer;
        color: rgb(255, 226, 243);
    }
    
    .nav-right {
        margin-left: 10px;
        margin-top: 10px;
        display: flex;
        flex-direction: row;

        img {
            height: 40px;
            width: 40px;
        }
    }

    @media screen and (max-width: 500px) {
        .menu-button {
            display: block;
        }

        .nav-right {
            display: none;
            margin-left: 0;
            width: 100%;
            flex-direction: column;
            align-items: center;
        }

        .show {
            display: flex;
        }
    }
}

.show-menu {
    display: flex;
    height: 100%;
    justify-content: center;
}