.Home {
    text-align: center;
    min-height: 100vh;

    #hero {
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 300px 0px 0px;

        #msg {
            font-size: 75px;
            font-style: oblique;
            font-weight: normal;
        }

        #arrow {
            font-size: 50px;
            font-weight: normal;
        }
    }

    #about {
        display: flex;
        justify-content: center;
        margin: 300px 0px 0px;

        .about-intro {
            font-size: 50px;
            display: inline;

            #emily-headshot {
                width: 15vw;
                height: 15vw;
                border: 5px solid;
                border-radius: 200px;
                border-color: white;
                opacity: 0.8;
            }
        }

        .about-info {          
            display: flex;
            justify-content: space-between;
            align-items: baseline;

            p {
                font-size: 75px;
                font-weight: bold;
                font-style: oblique;
                margin: 0px 0px 0px 25px;
            }

            #bullets {
                padding-left: 25px;
                font-size: 25px;
                list-style-type: none;
                list-style-position: inside;
                text-align: left;
            }

            li:before {
                content: "â˜† ";
            }
        }
    }

    #involvements {
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 0px 0px 300px;
        font-size: 50px;

        .title {
            font-style: oblique;
        }
    }
}