.skill-card {
    border-radius: 20px;
    padding: 20px;
    width: 20vw;
    background-color: white;
    margin: 10px;
    color: black;
    box-shadow: 0px 8px 15px #ffa2b01a;
    text-align: center;
    position: relative;

    #image {
        height: 5vw;
        width: 5vw;
    }

    h4 {
        font-size: 25px;
        color: #87abfa;
        letter-spacing: .1rem;
    }

    p {
        font-size: 15px;
        color: grey;
    }
}